<template>
  <Layout>
    <SearchInput
      :handleOnEnterPress="handleSubmit"
      :type="type"
    />
    <SearchButton
      @click.native="handleSubmit"
       :type="type"
    />
  </Layout>
</template>

<script>
import SearchInput from '@/components/Input/Search';
import SearchButton from '@/components/Button/Search';
import Layout from './Layout';

export default {
  props: {
    type: {
      default() {
        return 'home';
      },
    },
  },
  components: {
    Layout,
    SearchInput,
    SearchButton,
  },
  methods: {
    handleSubmit(e) {
      const keyword = e.target.value;
      this.$router.push({
        name: 'SearchResult',
        query: { keyword },
      });
    },
  },
};
</script>


