<template>
  <Layout>
    <ExpandButton />
    <List>
      <Row>
        <Item
          name="All years"
        />
      </Row>

      <Row>
        <Item
          name="Last 24 hours"
        />
        <Item
          name="Exact date"
        />
        <Item
          name="Date range"
        />
      </Row>

      <Row>
        <Item
          name="Last week"
        />
        <Item
          name="Last 2 weeks"
        />
      </Row>

      <Row>
        <Item
          name="Last month"
        />
        <Item
          name="Last 2 months"
        />
        <Item
          name="Last 3 months"
        />
        <Item
          name="Last 6 months"
        />
      </Row>

      <Row>
        <Item
          name="Last year"
        />
        <Item
          name="Last 2 years"
        />
      </Row>
    </List>
   
  </Layout>
</template>

<script>
import Layout from './Layout';
import ExpandButton from './ExpandButton';
import List from './List';
import Row from './Row';
import Item from './Item';

export default {
  components: {
    Layout,
    ExpandButton,
    List,
    Row,
    Item,
  },
  data() {
    return {
      isOpen: false,
      selected: 'All years',
    };
  },
  mounted() {
    document.body.addEventListener('click', () => {
      if (this.isOpen) {
        this.toggleVisibility();
      }
    });
  },
  methods: {
    toggleVisibility() {
      this.isOpen = !this.isOpen;
    },
  },
};
</script>



